import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'p-modal',
  styleUrl: 'p-modal.scss',
  shadow: true,
})
export class PModal {
  @Prop({
    mutable: true,
    reflect: true,
  })
  @Prop()
  header: string;
  @Prop() isopen: boolean;


  // Functions
  private handleCancel = () => {
    this.isopen = false;
  };

  render() {
    return (
      <div class={this.isopen ? 'modal-wrapper isopen' : 'modal-wrapper'}>
        <div class="modal-overlay" onClick={this.handleCancel} />
        <div class="modal">
          <div class="header">
            <div class="close" >
              <p-svg class="icon" onClick={this.handleCancel} name='CLOSE' width="14" height="14" color='black'>
              </p-svg>
            </div>
            <h6>{this.header}</h6>
          </div>
          <div class="body">
            <slot />
          </div>
          <div class="footer">
            <p-button type='full' onClick={this.handleCancel} text='Entendido' color='secondary'  />
          </div>
        </div>
      </div>
    );
  }
}
